const JsyServer = require("../../../services/jsy-server.js");
let _this,
    ids,
    type = 0,
    adding = true;

export default {
  data() {
    return {
      selectContent: [],
      bindSelect: false,
      //是否点开搜素类别
      list: [],
      optionIndex: 0
    };
  },

  onPullDownRefresh: function () {
    this.getManagerList({});
  },
  onLoad: function (options) {
    adding = true;
    _this = this;
    let idList = JSON.parse(options.optionList);
    ids = idList.join(",");
    // type = options.type || 0;
    this.getManagerList({});
    this.getAreaList();
  },
  components: {},
  props: {},
  methods: {
    // 获得区域列表
    getAreaList: function () {
      JsyServer.getAreaList().then(res => {
        let selectContent = [{
          name: "全部区域",
          id: false
        }].concat(res.data);
        console.log("selectContent", selectContent);
        this.setData({
          selectContent: selectContent
        });
      }).catch(err => {
        console.log("err=>", err);
      });
    },
    // 获取区域经理列表
    getManagerList: function (data) {
      // JsyServer.getManagerList(data).then(res => {
      //   wx.stopPullDownRefresh();

      //   _this.setData({
      //     list: res.data
      //   });

      //   console.log("res---", res);
      // }).catch(err => {
      //   console.log("err---", err);
      // });
	  this.list = [{name:"陈英萍",customerNum: 848,id:"3sff2r29f923fjsjkdf2323r3",deputyNum: 2,areaName: "石狮"},
				  {name:"陈英萍",customerNum: 848,id:"3sff2r29f923fjsjkdf2323r3",deputyNum: 2,areaName: "石狮"},
				  {name:"陈英萍",customerNum: 848,id:"3sff2r29f923fjsjkdf2323r3",deputyNum: 2,areaName: "石狮"},
				  {name:"陈英萍",customerNum: 848,id:"3sff2r29f923fjsjkdf2323r3",deputyNum: 2,areaName: "石狮"},
				  {name:"陈英萍",customerNum: 848,id:"3sff2r29f923fjsjkdf2323r3",deputyNum: 2,areaName: "石狮"},
				  {name:"陈英萍",customerNum: 848,id:"3sff2r29f923fjsjkdf2323r3",deputyNum: 2,areaName: "石狮"},
				  {name:"陈英萍",customerNum: 848,id:"3sff2r29f923fjsjkdf2323r3",deputyNum: 2,areaName: "石狮"}]
    },
    bindSearch: function (e) {
      let content = e.detail.value;
      let areaid = this.selectContent[0].id;
      let data = {
        content
      };

      if (areaid) {
        data.areaid = areaid;
      }

      this.getManagerList(data);
    },
    // 点击选项框
    bindSelect: function () {
      this.setData({
        bindSelect: !this.bindSelect
      });
    },
    // 点击选项框中的选项(客户列表)
    bindSelectContent: function (e) {
      let index = e.currentTarget.dataset.index;
      let selectContent = this.selectContent;
      let obj = selectContent[0];
      selectContent[0] = selectContent[index];
      selectContent[index] = obj;
      this.setData({
        selectContent: selectContent
      });
      if (index == 0) return;

      if (selectContent[0].id) {
        this.getManagerList({
          areaid: selectContent[0].id
        });
      } else {
        this.getManagerList({});
      }
    },
    tapPitchOn: function (e) {
      let index = e.currentTarget.dataset.index;
      this.setData({
        optionIndex: index
      });
    },
    confirm: function () {
      let index = this.optionIndex;
      let managerid = this.list[index].id;
      let data = {
        managerid,
        ids
      };
      console.log(ids, managerid);

      if (!adding) {
        return;
      }

      if (type == 0) {
        this.allocationDeputy(data);
      } else {
        this.allocationCustomer(data);
      }
    },
    // 分配帮办
    allocationDeputy: function (data) {
      adding = false;
      JsyServer.allocationDeputy(data).then(res => {
        let pages = getCurrentPages();
        let prevPage = pages[pages.length - 2];
        prevPage.setData({
          setOver: true
        });
        wx.showToast({
          title: '分配成功'
        });
        let timer = setTimeout(() => {
          wx.navigateBack({});
        }, 1000);
      }).catch(err => {
        adding = true;
        wx.showToast({
          title: '分配失败',
          icon: 'none'
        });
      });
    },
    // 分配客户
    allocationCustomer: function (data) {
      adding = false;
      JsyServer.allocationCustomer(data).then(res => {
        let pages = getCurrentPages();
        let prevPage = pages[pages.length - 2];
        prevPage.setData({
          setOver: true
        });
        wx.showToast({
          title: '分配成功'
        });
        let timer = setTimeout(() => {
          wx.navigateBack({});
        }, 1000);
      }).catch(err => {
        adding = true;
        wx.showToast({
          title: '分配失败',
          icon: 'none'
        });
      });
    },
    cancel: function () {
      wx.navigateBack({});
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
